

$colours:
"oneToOne" #FF0000,
"buyer" #001EFF,
"general" #00FF00,
"listing" #F6FF00;

@each $i in $colours{
.#{nth($i, 1)}-background {
    background: nth($i, 2) !important;
}
.#{nth($i, 1)}-color {
    color:nth($i, 2) !important;
}
}


.rbc-off-range-bg {
    background-color: cornsilk;
}


// customEventPopOver 
.customEventPopOver {
    max-width: max-content !important;
    padding: 10px;
    .card {
        border: 0px;
        .card-header {

            background: #fff;
        }
        .card-body {
           
            .label {
                text-align: left;
            }

            .notes {
                overflow: auto;
                max-width: 200px;
            }
        }
    }

}   

// Diary event
.wb-event {
    .label {
        min-width: 60px;
        margin-right: 5px;
        padding: 5px;
        display: -webkit-inline-box;
        display: -ms-inline-flexbox;
        display: inline-flex;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        border-radius:2px;
        background-color: #ebedf3;
        -webkit-border-radius:2px;
        -moz-border-radius:2px;
        -ms-border-radius:2px;
        -o-border-radius:2px;
}
}
.rbc-day-slot .rbc-event-label {
    font-weight: bold;
    display: flex;
    justify-content: space-evenly;
    padding-bottom: 6px;
}

.rbc-event-content {
    span.label {
        padding: 5px;
    }
}

.rbc-now{
    a {
    border-radius: 1000%;
    width: 20px;
    height: 20px;
    background: black;
    display: inline-block;
    /* line-height: 40px; */
    vertical-align: center;
    text-align: center;
    color: #ffffff !important;
    }

}
.rbc-toolbar {
    label {
        color: #373a3c;
        display: inline-block;
        margin: 0;
        text-align: center;
        vertical-align: middle;
        background: none;
        background-image: none;
        border: 1px solid #ccc;
        padding: .375rem 1rem;
        border-radius: 4px;
        line-height: normal;
        white-space: nowrap;
    }

    .navigation {
        button{
            padding: .2rem .5rem .2rem .5rem;
        }
    }

    .selectOption {
        padding: .4rem 1rem .4rem 2rem;
        height: unset;
    }
}

.diary-filter {
    .btn-group {
        width: 100%;
    }
}

.form-group {
    margin-bottom: unset;
}

.rbc-overlay-header {
    padding: .5rem !important;
    font-weight: 600;
    text-align: center;
}
.rbc-event-label {
    display: none !important;
}
.previewSmsEmail {
    .modal-dialog.modal-xl {
        height: max-content !important;
    }

}